













































































/*-------- for touch screens --------*/
html {
  /*cursor: none;*/
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
a {
  /*cursor: none;*/
}

/*-------- fonts --------*/
@font-face {
  font-family: "Avenir-Medium";
  src: local("fonts/Avenir-Medium.ttf");
}
@font-face {
  font-family: "Avenir-Heavy";
  src: local("fonts/Avenir-Heavy.ttf");
}

/*-------- common styles --------*/
body {
  letter-spacing: 2px;
  font-family: "Avenir-Medium";
}
.image-frame {
  border: 2px;
  border-style: solid;
  border-color: #0a3c5b;
  overflow: hidden;
}
img.image-center {
  width: 100%;
  display: block;
  margin: auto;
}
#lightcase-nav a[class*='lightcase-icon-'].lightcase-icon-close {
  top: 30px;
  right: 30px;
  font-size: 64px;
}




